import $ from 'jquery'
import { fetch } from '@/util/request'
import tpl from './index.hbs'
import items from './items.hbs'
import './index.css'
class SuspiciousVehicle{
  constructor(el){
    this.bindEvent = this.bindEvent.bind(this)
    $(el).append(tpl())
    this.el = el
  }
  /**
   * 绑定前进后退事件
   * @param {number} activeCount 点击一次需要移动的元素个数
   * @return {undefined}
   */
  bindEvent(activeCount){
    const { el } = this
    let currentIndex = activeCount - 1
    // 上一组
    $(el).find('.prev').off().click(() => {
      const ul = $(el).find('.list ul')
      if(ul.is(':animated')){
        return
      } 
      currentIndex -= activeCount
      currentIndex = currentIndex < 0 ? 0 : currentIndex
      let itemWidth = ul.children().eq(0).outerWidth(true)
      itemWidth = Math.ceil(itemWidth)
      if(!currentIndex){
        for(let i = 0; i < activeCount;i++){
          ul.children().first().before(ul.children().last())        
        }  
        ul.css({'left':  `-=${itemWidth * activeCount}px`})        
      }
      ul.stop().animate({'left': `+=${itemWidth * activeCount}px`}, 500)
    })
    // 下一组
    $(el).find('.next').off().click(() => {
      const ul = $(el).find('.list ul')
      if(ul.is(':animated')){
        return
      } 
      currentIndex += activeCount
      currentIndex = currentIndex > ul.children().length - 1 ? ul.children().length - 1 : currentIndex
      let itemWidth = ul.children().eq(0).outerWidth(true)
      itemWidth = Math.ceil(itemWidth)
      if(currentIndex === ul.children().length - 1){
        for(let i = 0; i < activeCount;i++){
          ul.children().last().after(ul.children().first())        
        }  
        ul.css({'left':  `+=${itemWidth * activeCount}px`})        
      }
      ul.stop().animate({'left': `-=${itemWidth * activeCount}px`}, 500)
    })
  }
  render(){
    const { el, bindEvent } = this
    fetch('fetchSuspiciousVehicle', {type: 0}, data => {
      const result = data.suspiciousVehicle
      $(el).find('.value').html(result.total)
      const dataSet = result.list
      $(el).find('.list ul').html(items(dataSet))
      bindEvent(3)
    })
  }
}
export default SuspiciousVehicle



