import $ from 'jquery'
import './index.css'
import { fetch } from '@/util/request'
import mainHbs from './templates/main.hbs'
import videoListHbs from './templates/videoList.hbs'

export default class VideoPlate {
  /**
   * 创建一个全屏背景层，
   * 用于绑定事件，隐藏浮动元素
   * @return {void} void
   */
  initBgWrapper() {
    let bodyWidth = $('body').width()
    let bodyHeight = $('body').height()
    $('body').append('<div id="bgWrapper"></div>')
    $('#bgWrapper')
      .css({
        display: 'none',
        width: `${bodyWidth}px`,
        height: `${bodyHeight}px`,
        background: 'transparent',
        position: 'fixed',
        left: '0',
        top: '0'
      })
      .on('click', () => {
        $('.video-selector-tooltip').hide()
        $('#bgWrapper').hide()
      })
  }

  /**
   * 获取地理列表的 Promise
   * @param  {String} url  请求地址
   * @param  {Object} data 请求参数
   * @return {void} void
   */
  getJSON(url, data = {}) {
    return new Promise(resolve => {
      fetch(url, data, d => {
        resolve(d)
      })
    })
  }

  /**
   * 选择集的事件
   * @param  {String} selector 选择器 id
   * @return {void}   void
   */
  selectorEvents(selector) {
    // tooltip 相关节点
    // ===============
    let bgWrapper = $('#bgWrapper')
    let button = $(selector).find('.video-selector-button')
    let tooltip = $(selector).find('.video-selector-tooltip')
    let option = $(selector).find('.video-selector-tooltip-item')

    // 点击按钮展示选择框
    // ===============
    button.on('click', () => {
      // 更新 DOM
      bgWrapper.show()
      tooltip.show()
    })

    // 点击选择框内容
    // ============
    option.on('click', e => {
      let val = $(e.currentTarget).text()
      let id = e.currentTarget.dataset.id

      // 更新 DOM
      bgWrapper.hide()
      tooltip.hide()
      button.html(val)

      this.getJSON('VideoSrcFetch', { id })
        // 发起请求，渲染 video 列表
        // ======================
        .then(d => {
          let source = d.source
          let video = $(selector).find('.video-list-container')

          video.html(videoListHbs({ source }))

          let group = video.find('.video-list-group')
          let controls = video.find('.video-list-control')
          
          // 如果列表数量小于等于2，则隐藏控制按钮          
          if (group.children().length <= 2) {
            controls.hide()
          }
        })
        // 列表渲染后，绑定事件
        // =================
        .then(() => {
          let video = $(selector).find('.video-list-container')
          let item = video.find('.video-list-item')

          // 点击图片打开视频
          item.on('click', event => {
            // 打开视频
            window.open(event.currentTarget.dataset.src)
          })

          // 点击 向左 向右 箭头
          let leftBtn = video.find('.video-list-control-left')
          let rightBtn = video.find('.video-list-control-right')
          let group = video.find('.video-list-group')
          
          rightBtn.on('click', () => {   
            if (!$(group).is(':animated')) {
              group.animate({left: `-=${ item.width() + 10}`}, 300, () => {
                let groups = video.find('.video-list-group').children()
                groups.eq(0).insertAfter(groups.eq(groups.length - 1))
                group.css('left', '0px')
              })
            }
          })

          leftBtn.on('click', () => {
            if (!$(group).is(':animated')) {
              group.css('left', `-=${ item.width() + 10}`)
              let groups = video.find('.video-list-group').children()
              groups.eq(groups.length - 1).insertBefore(groups.eq(0))
              group.animate({left: '0px'}, 300)
            }
          })     

        })
    })
  }

  /**
   * 渲染
   * @return {void} void
   */
  render() {
    this.getJSON('PlaceListFetch')
      // 渲染三个列表页
      .then(d => {
        let place = d.place
        $('#videoPlate').append(mainHbs({
          group: [
            {
              selector: 'videoGroupNo1',
              place
            },
            {
              selector: 'videoGroupNo2',
              place
            }
          ]
        }))
      })
      // 页面渲染完成后，绑定事件
      .then(() => {
        this.initBgWrapper()
        let arr = [1, 2]
        arr.map(d => this.selectorEvents(`#videoGroupNo${d}`))

        // 模拟 option 点击事件
        arr.map(d => {
          $(`#videoGroupNo${d}`)
            .find('.video-selector-tooltip-item')
            .eq(d - 1)
            .click()
        })
      })
  }
}



