
import loader from '@/loader/loader'
import apis from './mock'
import './index.css'
import config from './config'
import Header from './components/header/index'
import AsideTab from './components/asideTab/index'
import VideoPlate from './components/videoPlate/index'
import ControlArea from './components/controlArea/index'
import CaseAnalysis from './components/caseAnalysis'

loader.load({
  apis: apis,
  config: config,
  init() {
    const caseAnalysis = new CaseAnalysis('body')
    const header = new Header()    
    const asideTab = new AsideTab()
    const videoPlate = new VideoPlate()
    const controlArea = new ControlArea()
  
    header.render()
    videoPlate.render()
    caseAnalysis.render()
    controlArea.render()
    
    asideTab.render() // 右边切换
  }
})



