import $ from 'jquery'
import './index.css'
import mainHbs from './index.hbs'
import { fetch } from '@/util/request'

export default class ControlArea {
  /**
   * 获取数据
   * @return {Object} 返回 Promise 对象
   */
  getJSON() {
    return new Promise(resolve => {
      fetch('ControlAreaFetch', d => {
        resolve(d)
      })
    })
  }

  /**
   * 渲染
   * @return {void} void
   */
  render() {
    this.getJSON()
      .then(d => {
        let option = {
          length: d.controlArea.length,
          controlArea: d.controlArea
        }
        $('#controlArea').append(mainHbs(option))
      })
      .then(() => {
        let group = $('.control-area-group')
        let item = $('.control-area-item')
        setInterval(() => {
          if (!$(group).is(':animated')) {
            group.animate({top: `-=${ item.height() + 36}`}, 500, () => {
              let groups = group.children()
              groups.eq(0).insertAfter(groups.eq(groups.length - 1))
              group.css('top', '0px')
            })          
          }
        }, 5000)
      })
  }
}



