import Mock from 'mockjs'
Mock.Random.extend({
  video() {
    let video = [
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading'
    ]
    return this.pick(video)
  },
  image() {
    let image = [
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
      'https://pic.36krcnd.com/201712/27061031/sfihigdfjrcx564d!heading',
    ]
    return this.pick(image)
  }
})
Mock.mock('@video')
Mock.mock('@image')

export default {
  'url': '/eyesight/videoSrc/:type/:id',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'source|1-5': [
        {
          'name|+1': ['人和立交1号机', '人和立交2号机', '人和立交3号机'], 
          'imgSrc': '@image',
          'src': '@video',
          'time': Mock.Random.date('mm:ss')
        }
      ] 
    }
  }
}



