let place = ['人和立交', '新牌坊立交', '沙坪坝汽车站', '江北立交', '观音桥大融城']
export default {
  'url': '/placeList/:type',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'place|5': [
        {
          'id|0-1000': Number('@id'),
          'name|+1': place 
        }
      ] 
    }
  }
}



