import $ from 'jquery'
import * as d3 from 'd3'
import tpl from './index.hbs'
import './index.css'
import animateIcon from '../images/animate-icon.png'
class TotalRatio{
  constructor(el){
    this.drawAnimation = this.drawAnimation.bind(this)
    this.drawOneLine = this.drawOneLine.bind(this)
    this.aDot = this.aDot.bind(this)
    this.structLineDots = this.structLineDots.bind(this)
    $(el).html(tpl())
    this.svg = d3.select(el).select('svg')
    this.drawAnimation()
  }
  /**
   * 绘制左上角动效
   * @return {undefined}
   */
  drawAnimation() {
    const { svg, drawOneLine, aDot } = this
    svg.append('image')
      .attr('xlink:href', animateIcon)
      .attr('x', 95)
      .attr('y', 0)
      .attr('height', 104)
      .attr('width', 22)
    // 画两条上下运动的曲线
    const linePath = d3.svg.line()
      .x(function(d) {
        return d[0]
      })
      .y(function(d) {
        return d[1]
      })
      .interpolate('cardinal')

    const randomLineDots = svg.append('g').attr('class', 'random-lines-dots')

    // 画两条动态曲线
    for (let i = 0; i < 2; i++) {
      drawOneLine(randomLineDots, linePath)
    }

    // 画散点，并动画
    for(let i = 0;i < 20; i++){
      const dot = randomLineDots.append('circle')
      aDot(dot)
    }
  }

  /**
   * 画一条左上角的动画曲线
   * @param {object} randomLineDots 包含曲线和点的g对象
   * @param {object} linePath 生成曲线要用到的路径生成器
   * @return {undefined}
   */
  drawOneLine(randomLineDots, linePath) {
    const { structLineDots } = this
    const line = randomLineDots.append('path')
      .attr({
        'filter': 'url(#GaussianBlur)',
        'stroke': '#f8cc02',
        'stroke-width': 3,
        'fill': 'transparent',
        'opacity': 0.5,
        'd': function() {
          return linePath(structLineDots())
        }
      });

    (function animationLine() {
      line.transition()
        .duration(2000)
        .ease('linear')
        .attr('d', function() {
          const dots = structLineDots()
          return linePath(dots)
        })
        .each('end', animationLine)
    }())
  }

  /**
   * 返回一组点，画左上角动画曲线的时候使用，4个点，最后一个点的位置始终是不变的
   * @return {array} dots 返回一组用于画线的点
   */
  structLineDots() {
    const dots = []
    for (let i = 0; i < 3; i++) {
      const dot = []
      const random = Math.random()
      dot.push(i * 33 + 9)
      dot.push(94 * random)
      dots.push(dot)
    }
    dots.push([105, 45])
    return dots
  }

  /**
   * 左上角产生一个点，并指定它的动画
   * @param {d3} dot 点的d3对象
   * @return {undefined}
   */
  aDot(dot) {
    const { aDot } = this
    var random = Math.random()
    dot
      .attr({
        'cx': 105,
        'cy': 45,
        'r': 5,
        'opacity': 0,
        'fill': '#fffb11',
        'transform': `rotate(${ 45 + random * 90 } 105 45)`
      })
      .transition()
      .duration(3000)
      .delay(3000 * Math.random())
      .ease('linear')
      .attr({
        'transform': `rotate(${ 45 + random * 90 } 105 45) translate(0,90)`,
        'opacity': 0.1,
        'r': 2
      })
      .each('start',function(){
        dot.attr('opacity', 1)
      })
      .each('end', function(){
        aDot(dot)
      })
  }
  render(data){
    $('.value').text(data)
  }
}
export default TotalRatio



