import $ from 'jquery'
import { fetch } from '@/util/request'
import tpl from './index.hbs'
import './index.css'
import TotalRatio from './total'
import DangerRatio from './dangerRatio'
import SuspiciousPerson from './suspiciousPerson'
import SuspiciousVehicle from './suspiciousVehicle'
class CaseAnalysis{
  constructor(el){
    $(el).append(tpl())
    this.dangerRatio = new DangerRatio('.danger-ratio')
    this.total = new TotalRatio('.total')
    this.suspiciousPerson = new SuspiciousPerson('.suspicious-person')
    this.suspiciousVehicle = new SuspiciousVehicle('.suspicious-vehicle')
  }
  render(){
    const { total, dangerRatio, suspiciousPerson, suspiciousVehicle } = this
    
    suspiciousPerson.render()
    suspiciousVehicle.render()
    fetch('fetchDangerRatio',{type: 0}, data => {
      const result = data.dangerRatio
      total.render(result.total)
      dangerRatio.render(result.list)
    })
  }
}
export default CaseAnalysis



