import $ from 'jquery'
import tpl from './index.hbs'
import './index.css'
import { SplitBar } from '@/charts/index'
const config = {
  height: 345,
  width: 380
}
class DangerRatio{
  constructor(el){
    $(el).append(tpl(config))
    this.splitBar = new SplitBar()

  }
  render(data){
    const { splitBar } = this
    splitBar.render(data)
  }
}
export default DangerRatio



