import $ from 'jquery'
import './index.css'
import asideTabHbs from './index.hbs'

export default class AsideTab {
  constructor() {
    $('#asideTab').append(asideTabHbs())
  }

  /**
   * 绑定事件
   * @param  {Number} mapIndex 下标
   * @return {void}   void
   */
  bindEvent(mapIndex) {
    $('.aside-tab-item').on('click', e => {
      let that = $(e.currentTarget)
      let type = e.currentTarget.dataset.type 

      // 判断按钮是否被选中
      let status
      if (that.attr('class').indexOf('emphasis') > 0) {
        status = true
      } else {
        status = false
      }
      
      switch(type) {
      case '1': // 警力        
        if (!status) {          
          that.attr('class', 'aside-tab-item emphasis')
          console.log('警力进入')
        } else {
          that.attr('class', 'aside-tab-item')
          console.log('警力退出')
        }
        break
      case '2': // 摄像头
        if (!status) {          
          that.attr('class', 'aside-tab-item emphasis')
          console.log('摄像头进入')
        } else {
          that.attr('class', 'aside-tab-item')
          console.log('摄像头退出')
        }
        break
      case '3': // 信号灯
        if (!status) {          
          that.attr('class', 'aside-tab-item emphasis')
          console.log('信号灯进入')
        } else {
          that.attr('class', 'aside-tab-item')
          console.log('信号灯退出')
        }
        break
      default:
        console.log('0') // 默认0
      }

      // 获取类型
      let getCategorys = $('.aside-tab-group').find('.emphasis') 
      let saveCategorys = []
      getCategorys.map((d) => {
        saveCategorys.push(getCategorys[d].dataset.type)
      })
      saveCategorys = saveCategorys.join('&') 
      if(saveCategorys === '') {
        saveCategorys = '0'
      }
      mapIndex.render(1, saveCategorys)
    })
  }

  /**
   *  describe
   *  @param    {function}  mapIndex 地图初始化
   *  @return   {[type]}   [description]
   */
  render(mapIndex) {    
    this.bindEvent(mapIndex)
    // 默认不选种
    // $('.aside-tab-item').eq(0).click()
  }
}



