let place = ['人和立交实施管控', '新牌坊立交实施管控', '沙坪坝汽车站实施管控', '黄泥磅立交实施管控', '朝阳路实施管控']
export default {
  'url': '/controlArea/:type',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'controlArea|5': [
        {
          'id|0-1000': Number('@id'),          
          'name|+1': place,
          'value': '@csentence'
        }
      ] 
    }
  }
}



